/**
 * External Dependencies
 */
import { useState, useCallback, useRef, useEffect } from "@wordpress/element";
/**
 * Hook for managing location state and form integration
 */
export const useLocationState = (defaultLocation, showMap = true) => {
    const [location, setLocation] = useState(defaultLocation || null);
    const [isMapVisible, setIsMapVisible] = useState(false);
    const [searchQuery, setSearchQuery] = useState("");
    const fieldOnChangeRef = useRef(null);
    // Initialize map when component mounts or location changes
    useEffect(() => {
        if (location && showMap) {
            setIsMapVisible(true);
        }
        else if (!location) {
            setIsMapVisible(false);
        }
    }, [location, showMap]);
    // Set location and update form
    const setLocationAndUpdateForm = useCallback((newLocation) => {
        setLocation(newLocation);
        if (newLocation && showMap) {
            setIsMapVisible(true);
        }
        else {
            setIsMapVisible(false);
        }
        // Update form value
        if (fieldOnChangeRef.current) {
            fieldOnChangeRef.current(newLocation);
        }
    }, [showMap]);
    // Clear location
    const clearLocation = useCallback(() => {
        setLocation(null);
        setIsMapVisible(false);
        setSearchQuery("");
        // Clear form value
        if (fieldOnChangeRef.current) {
            fieldOnChangeRef.current(null);
        }
    }, []);
    // Update search query
    const updateSearchQuery = useCallback((query) => {
        setSearchQuery(query);
        // If query is empty, clear location
        if (!query.trim()) {
            setLocation(null);
            setIsMapVisible(false);
            if (fieldOnChangeRef.current) {
                fieldOnChangeRef.current(null);
            }
        }
    }, []);
    // Set field onChange function
    const setFieldOnChange = useCallback((onChange) => {
        fieldOnChangeRef.current = onChange;
    }, []);
    // Initialize with existing form value
    const initializeWithFormValue = useCallback((formValue) => {
        if (formValue) {
            if (typeof formValue === "string") {
                setSearchQuery(formValue);
                // Don't show map for string-only values in edit mode
                // User needs to search and select to get coordinates
            }
            if (formValue.address && formValue.lat && formValue.lng) {
                // LocationValue object with coordinates
                setSearchQuery(formValue.address);
                setLocation(formValue);
                setIsMapVisible(true);
            }
        }
    }, []);
    return {
        location,
        isMapVisible,
        searchQuery,
        setLocationAndUpdateForm,
        clearLocation,
        updateSearchQuery,
        setFieldOnChange,
        initializeWithFormValue,
        fieldOnChangeRef,
    };
};
