import { jsx as _jsx } from "react/jsx-runtime";
/**
 * Wordpress Dependencies
 */
import { Fragment } from "@wordpress/element";
/**
 * Internal Dependencies
 */
import { useModuleStatus } from "../hooks/useModuleStatus";
import { Skeleton } from "@/shadcn/components/ui";
/**
 * Simple component that conditionally renders children based on module status
 * Automatically fetches module data if not present
 * Returns children if module is enabled, null otherwise
 */
export const ModuleConditional = ({ moduleName, children, skeletonClassName, }) => {
    const { isEnabled, isLoading } = useModuleStatus(moduleName);
    // Show skeleton while loading modules data
    if (isLoading) {
        return _jsx(Skeleton, { className: `h-12 w-full ${skeletonClassName}` });
    }
    // Return children if module is enabled, null otherwise
    return isEnabled ? _jsx(Fragment, { children: children }) : null;
};
