/**
 * WordPress dependencies
 */
import { useCallback, useState } from "@wordpress/element";
import { __ } from "@wordpress/i18n";
import { DEFAULT_SLOT_TIME, validateSlotUniqueness, validateTimeRange, } from "../constants";
/**
 * Hook for managing slot operations
 *
 * @param schedule - Current day schedule
 * @param onUpdate - Callback to update the day schedule
 * @returns Slot management functions and error states
 */
export const useSlotManagement = (schedule, onUpdate) => {
    const [errors, setErrors] = useState([]);
    /**
     * Generate a unique default slot that doesn't conflict with existing slots
     */
    const generateUniqueSlot = useCallback(() => {
        const existingSlots = schedule.slots;
        // Try different time slots until we find a unique one
        const timeSlots = [
            { start: "12:00 PM", end: "1:00 PM" },
            { start: "1:00 PM", end: "2:00 PM" },
            { start: "2:00 PM", end: "3:00 PM" },
            { start: "3:00 PM", end: "4:00 PM" },
            { start: "4:00 PM", end: "5:00 PM" },
            { start: "11:00 AM", end: "12:00 PM" },
            { start: "10:00 AM", end: "11:00 AM" },
            { start: "5:00 PM", end: "6:00 PM" },
        ];
        // Find the first time slot that doesn't exist
        for (const timeSlot of timeSlots) {
            if (validateSlotUniqueness(timeSlot, existingSlots)) {
                return timeSlot;
            }
        }
        // If all predefined slots are taken, generate a unique one
        let hour = 9; // Start from 9 AM
        while (hour < 18) { // Up to 6 PM
            const startTime = `${hour}:00 ${hour < 12 ? "AM" : "PM"}`;
            const endTime = `${hour + 1}:00 ${hour + 1 < 12 ? "AM" : "PM"}`;
            const newSlot = { start: startTime, end: endTime };
            if (validateSlotUniqueness(newSlot, existingSlots)) {
                return newSlot;
            }
            hour++;
        }
        // Fallback to default
        return DEFAULT_SLOT_TIME;
    }, [schedule.slots]);
    /**
     * Add new slot with validation
     */
    const handleAddSlot = useCallback(() => {
        const uniqueSlot = generateUniqueSlot();
        const newErrors = [];
        // Validate that start and end times are valid (different and end > start)
        if (!validateTimeRange(uniqueSlot.start, uniqueSlot.end)) {
            newErrors.push({
                type: "time_range",
                message: __("Slot end time must be greater than start time", "wpcafe"),
                field: "slot",
            });
        }
        // If there are errors, set them and return
        if (newErrors.length > 0) {
            setErrors(newErrors);
            return;
        }
        // Clear errors if validation passes
        setErrors([]);
        const newSlots = [...schedule.slots, uniqueSlot];
        onUpdate(Object.assign(Object.assign({}, schedule), { slots: newSlots }));
    }, [schedule, onUpdate, generateUniqueSlot]);
    /**
     * Update slot with validation
     */
    const handleSlotUpdate = useCallback((index, updatedSlot) => {
        const newErrors = [];
        // Create a temporary array without the current slot being updated
        const otherSlots = schedule.slots.filter((_, i) => i !== index);
        // Validate that the updated slot doesn't already exist
        if (!validateSlotUniqueness(updatedSlot, otherSlots)) {
            newErrors.push({
                type: "duplicate_slot",
                message: __("This slot already exists", "wpcafe"),
                field: "slot",
            });
        }
        // Validate that start and end times are valid (different and end > start)
        if (!validateTimeRange(updatedSlot.start, updatedSlot.end)) {
            newErrors.push({
                type: "time_range",
                message: __("Slot end time must be greater than start time", "wpcafe"),
                field: "slot",
            });
        }
        // If there are errors, set them and return
        if (newErrors.length > 0) {
            setErrors(newErrors);
            return;
        }
        // Clear errors if validation passes
        setErrors([]);
        const newSlots = [...schedule.slots];
        newSlots[index] = updatedSlot;
        onUpdate(Object.assign(Object.assign({}, schedule), { slots: newSlots }));
    }, [schedule, onUpdate]);
    /**
     * Delete slot
     */
    const handleSlotDelete = useCallback((index) => {
        const newSlots = schedule.slots.filter((_, i) => i !== index);
        onUpdate(Object.assign(Object.assign({}, schedule), { slots: newSlots }));
    }, [schedule, onUpdate]);
    return {
        handleAddSlot,
        handleSlotUpdate,
        handleSlotDelete,
        errors,
        clearErrors: () => setErrors([]),
    };
};
export default useSlotManagement;
