/**
 * WordPress dependencies
 */
import { useState, useLayoutEffect, useRef, } from "@wordpress/element";
/**
 * External dependencies
 */
import { useSearchParams } from "react-router-dom";
/**
 * Internal dependencies
 */
import { scrollTop } from "@/helpers";
export const useNavTabs = ({ menu, desktopBreakpoint = 768, }) => {
    const [searchParams, setSearchParams] = useSearchParams();
    const currentTab = searchParams.get("tab") || "general";
    const handleTabChange = (tab) => {
        searchParams.set("tab", tab);
        setSearchParams(searchParams);
        scrollTop();
    };
    const parents = menu;
    const containerRef = useRef(null);
    const itemRefs = useRef({});
    const [visibleCount, setVisibleCount] = useState(parents.length);
    // Factory to provide a ref callback for each tab so the caller (NavTab) can
    // attach it to the invisible "ghost" measuring elements. Without this the
    // measuring loop never receives widths and falls back to a single tab.
    const getMeasureRef = (tab) => (el) => {
        itemRefs.current[tab] = el;
    };
    useLayoutEffect(() => {
        const calc = () => {
            if (!containerRef.current)
                return;
            const isMobile = window.innerWidth < desktopBreakpoint;
            if (!isMobile) {
                if (visibleCount !== parents.length)
                    setVisibleCount(parents.length);
                return;
            }
            const containerWidth = containerRef.current.clientWidth;
            const GAP = 8; // from gap-2
            const OVERFLOW_BTN = 44; // approx more button width
            let used = 0;
            let count = 0;
            for (let i = 0; i < parents.length; i++) {
                const item = parents[i];
                if (!item)
                    continue;
                const el = itemRefs.current[item.tab];
                if (!el)
                    continue;
                const w = el.offsetWidth;
                const remaining = parents.length - (i + 1);
                const reserve = remaining > 0 ? OVERFLOW_BTN : 0;
                if (used + w + (count > 0 ? GAP : 0) + reserve <=
                    containerWidth) {
                    used += w + (count > 0 ? GAP : 0);
                    count++;
                }
                else {
                    break;
                }
            }
            if (count === 0)
                count = 1;
            if (count !== visibleCount)
                setVisibleCount(count);
        };
        requestAnimationFrame(calc);
        const handle = () => requestAnimationFrame(calc);
        window.addEventListener("resize", handle);
        const ro = new ResizeObserver(handle);
        if (containerRef.current)
            ro.observe(containerRef.current);
        return () => {
            window.removeEventListener("resize", handle);
            ro.disconnect();
        };
    }, [parents, visibleCount, desktopBreakpoint]);
    const visibleMobileParents = parents.slice(0, visibleCount);
    const overflowMobileParents = parents.slice(visibleCount);
    const isParentActive = (item) => {
        if (currentTab === item.tab)
            return true;
        if (item.children) {
            return item.children.some((c) => c.tab === currentTab);
        }
        return false;
    };
    return {
        currentTab,
        parents,
        visibleMobileParents,
        overflowMobileParents,
        isParentActive,
        handleTabChange,
        containerRef,
        getMeasureRef,
    };
};
export const useNavTabMeasureRef = (tab, refs) => (el) => {
    refs.current[tab] = el;
};
