/**
 * WordPress dependencies
 */
import { useState, useCallback } from "@wordpress/element";
/**
 * Simple modal hook for managing modal state
 */
export const useModal = () => {
    const [modalState, setModalState] = useState({
        isOpen: false,
        title: "",
        children: null,
    });
    const openModal = useCallback((title, children) => {
        setModalState({
            isOpen: true,
            title,
            children,
        });
    }, []);
    const closeModal = useCallback(() => {
        setModalState({
            isOpen: false,
            title: "",
            children: null,
        });
    }, []);
    return Object.assign(Object.assign({}, modalState), { openModal,
        closeModal });
};
