/**
 * Convert 12-hour time string to minutes for comparison
 *
 * @param timeString - Time string in format "h:mm AM/PM"
 * @returns number - Minutes since midnight
 *
 * @example
 * timeToMinutes("12:00 AM") // Returns 0
 * timeToMinutes("12:00 PM") // Returns 720
 * timeToMinutes("1:30 PM") // Returns 810
 */
const timeToMinutes = (timeString) => {
    const parts = timeString.split(" ");
    if (parts.length !== 2)
        return 0;
    const [time, period] = parts;
    if (!time || !period)
        return 0;
    const timeParts = time.split(":");
    if (timeParts.length !== 2)
        return 0;
    const [hoursStr, minutesStr] = timeParts;
    if (!hoursStr || !minutesStr)
        return 0;
    const hours = parseInt(hoursStr, 10);
    const minutes = parseInt(minutesStr, 10);
    if (isNaN(hours) || isNaN(minutes))
        return 0;
    let totalMinutes = hours * 60 + minutes;
    if (period.toUpperCase() === "PM" && hours !== 12) {
        totalMinutes += 12 * 60; // Add 12 hours for PM
    }
    else if (period.toUpperCase() === "AM" && hours === 12) {
        totalMinutes = minutes; // 12 AM is 0 hours
    }
    return totalMinutes;
};
/**
 * Validate that start and end times are valid (different and end > start)
 *
 * @param startTime - Start time string
 * @param endTime - End time string
 * @returns boolean - true if valid, false if invalid
 *
 * @example
 * validateTimeRange("10:00 AM", "5:00 PM") // Returns true
 * validateTimeRange("10:00 AM", "10:00 AM") // Returns false (same time)
 * validateTimeRange("12:45 AM", "12:05 AM") // Returns false (end < start)
 */
export const validateTimeRange = (startTime, endTime) => {
    // Check if times are the same
    if (startTime === endTime) {
        return false;
    }
    // Convert to minutes and compare
    const startMinutes = timeToMinutes(startTime);
    const endMinutes = timeToMinutes(endTime);
    // End time should be greater than start time
    return endMinutes > startMinutes;
};
/**
 * Validate that start and end times are not the same for day schedules
 * (Legacy function - now uses validateTimeRange)
 *
 * @param startTime - Start time string
 * @param endTime - End time string
 * @returns boolean - true if valid (different times), false if invalid (same times)
 *
 * @example
 * validateDayScheduleTimes("10:00 AM", "5:00 PM") // Returns true
 * validateDayScheduleTimes("10:00 AM", "10:00 AM") // Returns false
 */
export const validateDayScheduleTimes = (startTime, endTime) => {
    return validateTimeRange(startTime, endTime);
};
/**
 * Comprehensive validation for day schedule times with detailed error information
 *
 * @param startTime - Start time string
 * @param endTime - End time string
 * @returns object with validation result and error message
 *
 * @example
 * validateDayScheduleWithDetails("10:00 AM", "5:00 PM") // Returns { isValid: true, error: null }
 * validateDayScheduleWithDetails("10:00 AM", "10:00 AM") // Returns { isValid: false, error: "Start and end times cannot be the same" }
 * validateDayScheduleWithDetails("5:00 PM", "10:00 AM") // Returns { isValid: false, error: "End time must be after start time" }
 */
export const validateDayScheduleWithDetails = (startTime, endTime) => {
    // Check if times are the same
    if (startTime === endTime) {
        return {
            isValid: false,
            error: "Start and end times cannot be the same",
        };
    }
    // Convert to minutes and compare
    const startMinutes = timeToMinutes(startTime);
    const endMinutes = timeToMinutes(endTime);
    // Check if conversion was successful
    if (startMinutes === 0 && startTime !== "12:00 AM") {
        return {
            isValid: false,
            error: "Invalid start time format",
        };
    }
    if (endMinutes === 0 && endTime !== "12:00 AM") {
        return {
            isValid: false,
            error: "Invalid end time format",
        };
    }
    // End time should be greater than start time
    if (endMinutes <= startMinutes) {
        return {
            isValid: false,
            error: "End time must be after start time",
        };
    }
    // Additional validation: Check for reasonable business hours (optional)
    // This ensures the schedule is not too long (more than 24 hours)
    const durationMinutes = endMinutes - startMinutes;
    if (durationMinutes > 24 * 60) {
        return {
            isValid: false,
            error: "Schedule duration cannot exceed 24 hours",
        };
    }
    return {
        isValid: true,
        error: null,
    };
};
/**
 * Validate that slot doesn't already exist in the slots array
 *
 * @param newSlot - New slot object to add
 * @param existingSlots - Array of existing slots
 * @returns boolean - true if valid (no duplicate), false if invalid (duplicate exists)
 *
 * @example
 * validateSlotUniqueness(
 *   { start: "12:00 PM", end: "1:00 PM" },
 *   [{ start: "2:00 PM", end: "3:00 PM" }]
 * ) // Returns true
 * validateSlotUniqueness(
 *   { start: "12:00 PM", end: "1:00 PM" },
 *   [{ start: "12:00 PM", end: "1:00 PM" }]
 * ) // Returns false
 */
export const validateSlotUniqueness = (newSlot, existingSlots) => {
    return !(existingSlots === null || existingSlots === void 0 ? void 0 : existingSlots.some((slot) => slot.start === newSlot.start && slot.end === newSlot.end));
};
