// Enhanced content processor for embeds
export const processEmbedContent = (htmlContent) => {
    try {
        const parser = new DOMParser();
        const doc = parser.parseFromString(htmlContent, "text/html");
        // Find all wp-block-embed elements
        const embedBlocks = doc.querySelectorAll(".wp-block-embed");
        embedBlocks.forEach((block) => {
            var _a;
            const wrapper = block.querySelector(".wp-block-embed__wrapper");
            if (wrapper && !wrapper.querySelector("iframe")) {
                const urlText = (_a = wrapper.textContent) === null || _a === void 0 ? void 0 : _a.trim();
                if (urlText) {
                    // Check if it's a YouTube URL
                    const youtubeMatch = urlText.match(/https:\/\/(?:www\.)?youtu(?:\.be\/|be\.com\/watch\?v=|be\.com\/embed\/)([a-zA-Z0-9_-]{11})(?:\S+)?/);
                    if (youtubeMatch) {
                        const videoId = youtubeMatch[1];
                        const iframe = doc.createElement("iframe");
                        iframe.src = `https://www.youtube.com/embed/${videoId}?rel=0&modestbranding=1`;
                        iframe.setAttribute("frameborder", "0");
                        iframe.setAttribute("allowfullscreen", "");
                        iframe.setAttribute("allow", "accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture");
                        iframe.setAttribute("loading", "lazy");
                        iframe.setAttribute("title", "YouTube video player");
                        // Clear the wrapper and add the iframe
                        wrapper.innerHTML = "";
                        wrapper.appendChild(iframe);
                        return;
                    }
                    // Check if it's a Vimeo URL
                    const vimeoMatch = urlText.match(/https:\/\/(?:www\.)?vimeo\.com\/(\d+)/);
                    if (vimeoMatch) {
                        const videoId = vimeoMatch[1];
                        const iframe = doc.createElement("iframe");
                        iframe.src = `https://player.vimeo.com/video/${videoId}`;
                        iframe.setAttribute("frameborder", "0");
                        iframe.setAttribute("allowfullscreen", "");
                        iframe.setAttribute("allow", "autoplay; fullscreen; picture-in-picture");
                        iframe.setAttribute("loading", "lazy");
                        iframe.setAttribute("title", "Vimeo video player");
                        // Clear the wrapper and add the iframe
                        wrapper.innerHTML = "";
                        wrapper.appendChild(iframe);
                        return;
                    }
                    // If it's a plain URL but not supported, make it clickable
                    if (urlText.startsWith("http")) {
                        const link = doc.createElement("a");
                        link.href = urlText;
                        link.target = "_blank";
                        link.rel = "noopener noreferrer";
                        link.textContent = urlText;
                        wrapper.innerHTML = "";
                        wrapper.appendChild(link);
                    }
                }
            }
        });
        return doc.body.innerHTML;
    }
    catch (error) {
        // If processing fails, return original content
        console.warn("Failed to process embed content:", error);
        return htmlContent;
    }
};
// Utility functions
export const formatDate = (dateString) => {
    const date = new Date(dateString);
    return date.toLocaleDateString("en-US", {
        year: "numeric",
        month: "long",
        day: "numeric",
    });
};
