/**
 * WordPress dependencies
 */
import { useState, useCallback } from "@wordpress/element";
/**
 * Hook for managing UI state in schedule components
 *
 * @returns UI state management functions
 */
export const useScheduleUIState = () => {
    const [showBreaks, setShowBreaks] = useState({});
    const [showApplyToAll, setShowApplyToAll] = useState({});
    /**
     * Toggle break time visibility for a specific day
     */
    const toggleBreakVisibility = useCallback((dayKey) => {
        setShowBreaks((prev) => (Object.assign(Object.assign({}, prev), { [dayKey]: !prev[dayKey] })));
    }, []);
    /**
     * Set break time visibility for a specific day
     */
    const setBreakVisibility = useCallback((dayKey, visible) => {
        setShowBreaks((prev) => (Object.assign(Object.assign({}, prev), { [dayKey]: visible })));
    }, []);
    /**
     * Set apply to all button visibility for a specific day
     */
    const setApplyToAllVisibility = useCallback((dayKey, visible) => {
        setShowApplyToAll((prev) => (Object.assign(Object.assign({}, prev), { [dayKey]: visible })));
    }, []);
    /**
     * Get break visibility status for a day
     */
    const getBreakVisibility = useCallback((dayKey) => {
        return showBreaks[dayKey] || false;
    }, [showBreaks]);
    /**
     * Get apply to all visibility status for a day
     */
    const getApplyToAllVisibility = useCallback((dayKey) => {
        return showApplyToAll[dayKey] || false;
    }, [showApplyToAll]);
    return {
        showBreaks,
        showApplyToAll,
        toggleBreakVisibility,
        setBreakVisibility,
        setApplyToAllVisibility,
        getBreakVisibility,
        getApplyToAllVisibility,
    };
};
export default useScheduleUIState;
